<template>
  <div class="home">
    <div class="container mt-4">
      <div class="row">
        <!-- right nav -->

        <v-card class="col-md-12 pa-4 ">
          <div v-if="!loadData" class="text-center">
            <!-- <v-skeleton-loader
              v-bind="attrs"
              type="card-avatar, article, actions"
            ></v-skeleton-loader> -->
            <v-progress-circular
              :size="70"
              indeterminate
              color="red"
              :width="10"
            ></v-progress-circular>
          </div>

          <div v-else>
            <!-- <h3 class="col-md-12">
              <i class="mdi mdi-pencil"></i>
              تعديل : {{ title }}
            </h3> -->
            <!-- <v-divider></v-divider> -->
            <div>
              <div>
                <v-col cols="12" sm="12">
                  <v-text-field
                    v-model="title"
                    label="عنوان الفيديو"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-textarea
                    label="وصف الفيديو"
                    auto-grow
                    v-model="desc"
                    outlined
                    rows="3"
                    row-height="25"
                    shaped
                  ></v-textarea>
                </v-col>

                <label for=""
                  ><i class="mdi mdi-image"></i> إختر صورة الفيديو :</label
                >
                <v-file-input
                  chips
                  v-model="thumbnail"
                  placeholder=" إختر صورة الفيديو "
                  show-size
                  small-chips
                  truncate-length="50"
                ></v-file-input>

                <div class="text-center">
                  <v-btn
                    class=""
                    :loading="showProg"
                    color="success"
                    @click="updatePost()"
                  >
                    <i class="mdi mdi-pencil"></i> تحديت الفيديو
                  </v-btn>
                </div>
              </div>
            </div>
            <v-divider class="ma-4"></v-divider>
          </div>
        </v-card>
        <!-- nav left -->
        <!-- <NavLeft /> -->
      </div>
    </div>
  </div>
</template>

<script>
// import NavLeft from "@/components/pages/navleft";
export default {
  name: "Home",
  components: {
    // NavLeft,
  },
  data() {
    return {
      title: "",
      desc: "",
      video: "",
      thumbnail: "",
      loadData: false,
      showProg: false,
    };
  },
  methods: {
    tryagain() {
      this.showProg = false;
      this.mm = 0;
    },
    updatePost: function() {
      this.showProg = true;

      let formData = new FormData();
      formData.append("thumbnail", this.thumbnail);
      formData.append("desc", this.desc);
      formData.append("title", this.title);
      formData.append("status", true);
      formData.append("urlpost", this.$route.params.id);
      formData.append("userId", this.$store.getters.getUserId);
      const config = {
        headers: {
          Accept: "application/json,*/*",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Authorization",
          Authorization: "Bearer " + this.$store.getters.getToken,
        },
      };
      this.$http
        .put("/posts/update", formData, config)
        .then((res) => {
          this.showProg = false;
          this.$swal.fire("success", "تم التحديت بنجاح", "success");
          return res;
        })
        .catch((err) => {
          this.showProg = false;
          alert(err.message);
        });
    },
    dataPosts: function(param) {
      this.$http
        .get("/posts/watch/" + param)
        .then((res) => {
          this.loadData = true;
          this.title = res.data.title;
          this.desc = res.data.desc;
          if (
            this.$store.getters.getUserId != res.data.userId &&
            this.$store.getters.isAdmin != 1
          ) {
            this.$router.push({ name: "Home" });
          }
        })
        .catch((err) => {
          return err;
        });
    },
  },
  mounted() {
    this.dataPosts(this.$route.params.id);
  },
};
</script>
